import React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";

const ArticleItem = ({ article }) => {
    const { description, title, frontimage, date } = article.frontmatter
    const { slug } = article.fields
    return (
        <Link className="articles-item__link" to={`/articles${slug}`}>
            <div className="articles-item">            
                <div className="articles-item__img-wrapper">
                    { frontimage ? <GatsbyImage image={frontimage.childImageSharp?.gatsbyImageData} alt="" className="articles-item__img" /> : '' }
                </div>            
                <div className="articles-item__content">
                    <h3 className="articles-item__title">
                        {title}
                    </h3>
                    {/* <p className="articles-item__descripcion">
                            {description}
                    </p> */}
                    <p className="articles-item__footer">{date}</p>
                </div>            
            </div>
        </Link>
    );

}

export default ArticleItem